import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { RouteService } from '@core/services/route.service';

/**
 * If navigating from the Public pages, then it will redirect the user to a Public route.
 */

@Injectable({
	providedIn: 'root',
})
export class PublicGuard {
	constructor(private readonly routeService: RouteService, private readonly router: Router) {}

	canActivate(_route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
		const PREVIOUS_ROUTE_DATA = this.routeService.getRouteData();

		// Check if the route data property value of `isPublicPage` is equal to true, then redirect the user into a Public route.
		if (PREVIOUS_ROUTE_DATA?.isPublicPage) {
			this.router.navigateByUrl(`${APP_ROUTE.public.base}${state.url}`);

			return false;
		}

		return true;
	}
}
